<template>
    <div class="page">
        <div class="title">
            全部
        </div>
        <div class="filteBar">
            <div>
                <el-button type="primary" size="medium" @click="experimentAdd()">
                    <i class="el-icon-user"></i>批量批改
                </el-button>
            </div>
            <div>
                <el-select v-model="value" size="small" placeholder="实验一">
                    <el-option>信管1班</el-option>
                    <el-option>信管2班</el-option>
                </el-select>
                <el-select v-model="value" size="small" placeholder="全部">
                    <el-option>信管1班</el-option>
                    <el-option>信管2班</el-option>
                </el-select>
            </div>
        </div>
        <div class="table"  v-for="(item,index) in tableDatas" :key="index">
            <div class="title">
                {{item.name}}
            </div>
            <el-table
                :data="item.list"
                :row-class-name="tableRowClassName">
                <el-table-column
                prop="userNumber"
                label="学号">
                </el-table-column>
                <el-table-column
                prop="nickName"
                label="学生名称">
                </el-table-column>
                <el-table-column
                prop="status"
                label="状态">
                </el-table-column>
                <el-table-column
                label="操作"
                fixed="right">
                    <template slot-scope="scope">
                        <el-button
                        @click="routeToExperiment(scope.row)"
                        type="text"
                        size="small">
                        详情
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import spin from '@/components/spin.vue'
export default {
    props: {

    },
    data() {
        return {
            tableDatas: [
                {
                    name:"信管1班",
                    list: [{
                        userNumber: '2016-05-02',
                        nickName: '王小虎',
                        status: '上海市普陀区金沙江路 1518 弄'
                    }, {
                        userNumber: '2016-05-04',
                        nickName: '王小虎',
                        status: '上海市普陀区金沙江路 1517 弄'
                    }],
                },
                // --------------------
                {
                    name:"信管2班",
                    list: [{
                        userNumber: '2016-05-02',
                        nickName: '王小虎',
                        status: '上海市普陀区金沙江路 1518 弄'
                    }, {
                        userNumber: '2016-05-04',
                        nickName: '王小虎',
                        status: '上海市普陀区金沙江路 1517 弄'
                    }],
                }
            ],
        };
    },
    computed: {

    },
    created() {
        this.$http.get(`/teaching/teacher/achievement/list/${this.$route.params.courseId}`
        ).then((res) => { 
            if(res.data.code == "0"){
                this.tableDatas = res.data.data.tableDatas
            }else if (res.data.code == "1") {
                this.$router.push('/login'); 
            }else{
                this.$message({
                    message: res.data.msg,
                    type: 'error'
                });
            }
        })
        .catch(function (error) {
            console.log(error)
        })
    },
    mounted() {

    },
    watch: {

    },
    methods: {
    },
    components: {
        spin
    },
};
</script>

<style scoped lang="scss">
.page{
    padding:0 20px;
    .title{
        padding:10px;
        position: relative;
        border-bottom: 1px solid #E9E9E9;
        font-weight: bold;
    }
    .filteBar{
        padding:1em 0;
        display: flex;
        justify-content: space-between;
        .el-input{
            width: 200px;
        }
        .el-select{
            width: 120px;
        }
    }
    .table{
        padding: 1em;
    }
}
</style>



